* {
  box-sizing: border-box;
  margin: 1%;
}

h1 {
  font-family: 'Times New Roman', Times, serif;
}

/* Style the body */
body {
  font-family: Arial;
  margin: 0;
}


h2 {
  background-image: linear-gradient(rgb(98, 12, 155), rgb(23, 54, 146));
  color: rgb(255, 255, 255);
}

p {

  text-align: justify;

}

/* Header/logo Title */
.header {

  padding: 60px;
  text-align: center;
  background-image: linear-gradient(rgb(37, 10, 95), rgb(63, 19, 146));
  color: rgb(255, 255, 255);
}

/* Style the top navigation bar */
.navbar {

  display: flex;background-image: linear-gradient(rgb(56, 27, 118), rgb(140, 40, 164));
}

/* Style the navigation bar links */
.navbar a {
  color: #ffffff;
  padding: 14px 20px;
  text-decoration: none;
  text-align: center;
}

/* Change color on hover */
.navbar a:hover {
  background-color: #ddd;
  color: black;
}

/* Column container */
.row {
  display: flex;
  flex-wrap: wrap;
}

/* Create two unequal columns that sits next to each other */
/* Sidebar/left column */
.side {
  flex: 30%;
  background-color: #f1f1f1;
  padding: 20px;
}

/* Main column */
.main {
  flex: 70%;
  background-color: white;
  padding: 20px;
}

/* Fake image, just for this example */
.fakeimg {
  background-color: #aaa;
  width: 100%;
  padding: 20px;
}

.fakeimg {
  height: "200px"
}

/* Footer */
.footer {
  padding: 20px;
  text-align: center;
  background: #ddd;
}

/* Responsive layout - when the screen is less than 700px wide, make the two columns stack on top of each other instead of next to each other */
@media (max-width: 700px) {

  .row,
  .navbar {
    flex-direction: column;
  }
}